import React, { Component } from "react"
import Module from "./module.js"
import "../../styles/article.css"
import "../../styles/modules/bigimage.css"
import Animate from "react-smooth"

export default class BigImage extends Module {
  steps = [
    {
      style: {
        opacity: 0
      },
      duration: 400
    },
    {
      style: {
        opacity: 1,
        transform: "translate(0, 0)"
      },
      duration: 1000
    },
    {
      style: {
        transform: "translate(" + this.state.x + "100px)"
      },
      duration: 1200
    }
  ]
  render() {
    return (
      <div
        className={[
          "module ",
          "flex-direction-column",
          this.addModuleStyles()
        ].join("")}
      >
        <div className={[" big-image-container"].join(" ")}>
          <Animate steps={this.steps}>
            <img
              style={
                {
                  // transform: "translate3d(" + this.state.x + "px, 0,0)"
                  // transform: `translate($(this.state.xpx, $(this.state.ypx)`
                }
              }
              onMouseMove={this.onMouseMove.bind(this)}
              className="big-image"
              src={this.props.src}
              alt="Big Jitlada"
            />
          </Animate>
        </div>
        <div
          style={{ width: this.props.captionWidth }}
          className="big-image-caption"
        >
          {[this.props.caption, this.state.x, this.state.y]}
        </div>
      </div>
    )
  }
}
