import React, { Component } from "react"
import "../../styles/modules/module.css"

export default class Module extends Component {
  constructor(props) {
    super(props)
    this.state = {
      moduleStyles: "",
      x: 0,
      y: 0
    }
  }

  componentWillMount() {
    this.callModuleStyles()
  }

  addModuleStyles() {
    console.log("addmod")
    return this.state.moduleStyles
  }

  callModuleStyles() {
    // Add styles to this.
    this.setModuleWidth()
    this.isfirstModule()
    this.textAlign()
    this.fontWeight()
  }

  setModuleWidth() {
    // Variable module width.
    this.setState((state) => ({
      moduleStyles: state.moduleStyles + " "
    }))
  }

  isfirstModule() {
    if (this.props.firstModule === "true") {
      // Note the functional setstate syntax to maintain synchronity
      this.setState((state) => ({
        moduleStyles: state.moduleStyles + " first-module-margin"
      }))
    } else return
  }

  textAlign() {
    if (this.props.textAlign === "right") {
      this.setState((state) => ({
        moduleStyles: state.moduleStyles + "text-align-right"
      }))
      return "text-align-right"
    } else if (this.props.textAlign === "center") {
      this.setState((state) => ({
        moduleStyles: state.moduleStyles + " text-align-center"
      }))
      return "text-align-center"
    } else return
  }

  fontWeight() {
    if (this.props.fontWeight === "lighter") {
      this.setState((state) => ({
        moduleStyles: state.moduleStyles + " text-light"
      }))
    } else if (this.props.fontWeight === "bold") {
      this.setState((state) => ({
        moduleStyles: state.moduleStyles + " text-bold"
      }))
    } else return
  }

  onMouseMove(e) {
    this.setState({ x: e.nativeEvent.offsetX, y: e.nativeEvent.offsetY })
  }
}
